import { _, box, render, component, onKeyDown, AUTOFOCUS, Events, TrackByKey, key } from "ivi";
import { div } from "ivi-html";
import { Game, LEFT, RIGHT, UP, DOWN } from "./state";
function cellClassName(flags) {
    if (flags & 1 /* Body */) {
        if (flags & 2 /* Head */) {
            return "Cell body head";
        }
        return "Cell body";
    }
    else if (flags & 4 /* Food */) {
        return "Cell food";
    }
    return "Cell";
}
const CELL_SIZE = 30;
const GameView = component(() => {
    let game;
    const keyDown = onKeyDown((ev) => {
        switch (ev.keyCode) {
            case 37 /* ArrowLeft */:
                game.setNewDirection(LEFT);
                ev.preventDefault();
                break;
            case 38 /* ArrowUp */:
                game.setNewDirection(UP);
                ev.preventDefault();
                break;
            case 39 /* ArrowRight */:
                game.setNewDirection(RIGHT);
                ev.preventDefault();
                break;
            case 40 /* ArrowDown */:
                game.setNewDirection(DOWN);
                ev.preventDefault();
                break;
        }
        return;
    });
    return (props) => (game = props.v,
        div(game.gameOver ? "SnakeGame gameOver" : "SnakeGame", _, Events(keyDown, div("Grid", {
            tabIndex: 0,
            autofocus: AUTOFOCUS(true),
            style: {
                width: `${CELL_SIZE * game.grid.cols}px`,
                height: `${CELL_SIZE * game.grid.rows}px`,
            },
        }, TrackByKey(game.grid.cells.map((c, i) => key(i, div(cellClassName(c)))))))));
});
const CONTAINER = document.getElementById("app");
const GAME = new Game();
function tick() {
    GAME.updateState();
    render(GameView(box(GAME)), CONTAINER);
    setTimeout(tick, 100);
}
tick();
