export const LEFT = { x: -1, y: 0 };
export const RIGHT = { x: 1, y: 0 };
export const UP = { x: 0, y: -1 };
export const DOWN = { x: 0, y: 1 };
export class Snake {
    constructor(startPosition = { x: 0, y: 0 }) {
        this.body = [];
        this.grow = 4;
        this.body.push(startPosition);
    }
    get head() {
        return this.body[this.body.length - 1];
    }
    move(p) {
        this.body.push(p);
        if (this.grow) {
            this.grow--;
            return null;
        }
        else {
            const r = this.body[0];
            this.body = this.body.splice(1);
            return r;
        }
    }
}
export class Grid {
    constructor(rows = 20, cols = 20) {
        this.rows = rows;
        this.cols = cols;
        this.cells = new Array(rows * cols).fill(0);
    }
    pointToIndex(p) {
        return this.cols * p.y + p.x;
    }
    mark(p, flag) {
        this.cells[this.pointToIndex(p)] |= flag;
    }
    unmark(p, flag) {
        this.cells[this.pointToIndex(p)] &= ~flag;
    }
    createRandomFood() {
        let i = 0;
        do {
            i = (Math.random() * this.cells.length) | 0;
        } while (this.cells[i] !== 0);
        this.cells[i] |= 4 /* Food */;
    }
    isBodyAt(p) {
        return !!(this.cells[this.pointToIndex(p)] & 1 /* Body */);
    }
    isHeadAt(p) {
        return !!(this.cells[this.pointToIndex(p)] & 2 /* Head */);
    }
    isFoodAt(p) {
        return !!(this.cells[this.pointToIndex(p)] & 4 /* Food */);
    }
}
function addPoints(a, b) {
    return {
        x: a.x + b.x,
        y: a.y + b.y,
    };
}
function eqPoints(a, b) {
    return a.x === b.x && a.y === b.y;
}
function mod(a, b) {
    return ((a % b) + b) % b;
}
export class Game {
    constructor() {
        this.gameOver = false;
        this.startTime = 0;
        this.currentTime = 0;
        this.direction = { x: 1, y: 0 };
        this.grid = new Grid();
        this.snake = new Snake();
        this.newDirection = null;
        this.snake.body.forEach((p) => {
            this.grid.mark(p, 1 /* Body */);
        });
        this.grid.mark(this.snake.head, 2 /* Head */);
        this.grid.createRandomFood();
    }
    setNewDirection(dir) {
        this.newDirection = dir;
    }
    updateState() {
        if (!this.gameOver) {
            if (this.newDirection) {
                if (!eqPoints(addPoints(this.direction, this.newDirection), { x: 0, y: 0 })) {
                    this.direction = this.newDirection;
                }
                this.newDirection = null;
            }
            let nextPosition = addPoints(this.snake.head, this.direction);
            nextPosition = {
                x: mod(nextPosition.x, this.grid.cols),
                y: mod(nextPosition.y, this.grid.rows),
            };
            this.grid.unmark(this.snake.head, 2 /* Head */);
            const tail = this.snake.move(nextPosition);
            if (tail) {
                this.grid.unmark(tail, 1 /* Body */);
            }
            this.grid.mark(nextPosition, 2 /* Head */);
            if (this.grid.isBodyAt(nextPosition)) {
                this.gameOver = true;
            }
            else if (this.grid.isFoodAt(nextPosition)) {
                this.snake.grow = 3;
                this.grid.unmark(nextPosition, 4 /* Food */);
                this.grid.createRandomFood();
            }
            this.grid.mark(nextPosition, 1 /* Body */);
        }
    }
}
